.section {
  /* display: flex;
  justify-content: center;
  align-items: center; */

  width: 904px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 60px;

  background-color: #f8f8f8;
}

.container {
  text-align: center;
  margin-bottom: 32px;
}

.title {
  font-family: 'Nunito';
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 1;

  margin-bottom: 10px;

  color: rgba(0, 0, 0, 0.87);
}

.text {
  font-family: 'Nunito';
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.27;

  /* text-align: center; */

  color: rgba(0, 0, 0, 0.87);
}

.users_list {
  display: flex;
  flex-wrap: wrap;
}

.positionBtn {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .section {
    width: 704px;

    padding: 0 32px;
  }
}

@media screen and (max-width: 767px) {
  .section {
    width: 360px;

    padding: 0 16px;

    box-sizing: border-box;
  }

  .users_list {
    margin-bottom: 50px;
  }
}
